body {
    background-color: black;
}

#container {
    width: 600px;
    margin-left: auto;
    margin-right: auto;
}

#titleArea {
    margin-top: 3%;
    font-family: "ＭＳ 明朝";
    text-align: center;
    font-size: 2.5em;
    font-weight: bold;
    color: blueviolet;
}

#messageArea {
    height: 30px;
    color: orange;
    font-size: 1.5em;
    text-align: center;
    margin-top: 3%;
    margin-bottom: 3%;
}

#mainArea {
    display: flex;
    flex-wrap: nowrap;
}

#buttonContent {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    margin-top: 80px;

    input {
        width: 100px;
        margin: 10%;
    }
}

#slotContent {
    width: 400px;

    #spinArea {
        height: 130px;
        text-align: center;
    }

    #imageArea {
        text-align: center;
    }
}

.spinLabel {
    display: inline-block;
    color: gold;
    font-size: 3.5em;
    margin: 15px;
    width: 60px;
    height: 80px;
    border: solid 1px white;
}

#spinCountArea {
    width: 100px;
    color: blue;
    font-weight: bold;
    text-align: center;
    margin-top: 80px;

    #remainingCount {
        font-size: 1.2em;
    }
}

#resultArea {
    float: right;
    color: yellow;
    background-color: red;
    width: 100px;
    text-align: center;
    font-size: 1.6em;
    font-weight: bold;
}